@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
   background-color: rgb(249 249 249);
}
.App{
  text-align: center;
}
.h1{
  margin: 53px;
  padding: 23px;
  font-weight: bold;
  font-size: 51px;
}
@media only screen and (max-width: 600px){

  .h1 {
    margin: auto;
    padding: 23px;
    font-weight: bold;
    font-size: 51px;
  }
}


.cardholder{
    /* border:  2px solid black; */
    position: relative;
    width: 100vw;
    height : 70vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}
.btndiv{
    position : absolute;
    top: 25pc;
    left: 45pc;
}
.btn{
    border: none;
    padding: 35px;
}
.btn:hover{
    background-color: lightblue;
    border-radius: 10pc;
    
}
.btnl{
    height: 3pc;
    width: 3pc;
}
.btnr{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    height: 3pc;
    width: 3pc;
}
@media only screen and (max-width:1340px){
  
.cardholder{
    display: flex;
    justify-content: space-evenly;
    width: 100vw;
    height : 180vh;
}  
.btndiv {
    position: static;
}
    
}
.card{
    text-align: center;
    width: 500px;
    height: 250px;
    /* border: 2px solid black;    */
    border-radius: 15px;
    background-color: white;
}
.photo{
    position: relative;
    top: -21%;
    left: -10%;
    width: 100px;
    height: 100px;
    /* border: 2px solid black; */
    border-radius: 3pc;
}
.des{
    padding: 2px;
    margin: -30px 20px 0px 20px;
    font-weight: 900;
    font-size: 15px;
    background-color: white;
}
.nam{
    padding: 0px;
    margin-top: 19px;
    font-size: 15px;
    font-weight: bolder;
    background-color: white;

}
.pos{
   font-size: 18px;
   padding: 10px;
   background-color: white;

}
.pattern{
    height : 90px;
    width : 100px;
    background-color: rgba(255, 255, 255, 0.023);
    

}
.po{
    position: relative;
    top : -6pc;

}
.style1{
    position: relative;
    top: 9.5pc;
    left : 8pc;
}
.style2{
   position: relative;
   top: -5pc;
   left : 18pc;

}
.style3{
   position: relative;
   top : 9.5pc;
   left : -5pc;

}

@media only screen and (max-width: 1330px) {
    .po{
         
       position:static;
    
    }
    .style1{
        position: relative;
        top: -3.5pc;
        left: -4pc;
    }
    .style2{
        position: relative;
        top: -4pc;
        left: -4pc;
    
    }
    .style3{
       position: relative;
       top: -3.5pc;
       left: -3pc;
    
    }
    
}
